* {
  padding: 0;
  margin: 0;
}

.tabbar {
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #fff;
  border-top: 1px solid #ccc;
  padding: .3rem 0;
  display: flex;
  flex-direction: row;
}

.tabbar > div {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.tabbar > div img {
  width: 1.5rem;
}

.tabbar > div span {
  color: #999999;
  font-weight: 600;
  font-size: .8rem;
}
